export default {
	data() {
		return {
			autoplaySwiper: false ? true : false,
			intervalSwiper: false ? $template2.front.base.swiper.autoplay.delay : 5000,
			btnColor: ['#409eff', '#67c23a', '#909399', '#e6a23c', '#f56c6c', '#356c6c', '#351c6c', '#f093a9', '#a7c23a',
				'#104eff', '#10441f', '#a21233', '#503319'
			],
			id: '',
			userid: '',
			detail: {},
			swiperList: [],
			commentList: [],
			mescroll: null, //mescroll实例对象
			downOption: {
				auto: false //是否在初始化后,自动执行下拉回调callback; 默认true
			},
			upOption: {
				noMoreSize: 3, //如果列表已无数据,可设置列表的总数量要大于半页才显示无更多数据;避免列表数据过少(比如只有一条数据),显示无更多数据会不好看; 默认5
				textNoMore: '~ 没有更多了 ~',
			},
			hasNext: true,
			user: {},
			sfshIndex: -1,
			sfshOptions: ['通过', '不通过'],
			count: 0,
			timer: null,
			tableName:"",
		}
	},
	computed: {
		baseUrl() {
			return this.$base.url;
		},
		SecondToDate: function() {
			var time = this.count;
			if (null != time && "" != time) {
				if (time > 60 && time < 60 * 60) {
					time =
						parseInt(time / 60.0) +
						"分钟" +
						parseInt((parseFloat(time / 60.0) - parseInt(time / 60.0)) * 60) +
						"秒";
				} else if (time >= 60 * 60 && time < 60 * 60 * 24) {
					time =
						parseInt(time / 3600.0) +
						"小时" +
						parseInt(
							(parseFloat(time / 3600.0) - parseInt(time / 3600.0)) * 60
						) +
						"分钟" +
						parseInt(
							(parseFloat(
									(parseFloat(time / 3600.0) - parseInt(time / 3600.0)) * 60
								) -
								parseInt(
									(parseFloat(time / 3600.0) - parseInt(time / 3600.0)) * 60
								)) *
							60
						) +
						"秒";
				} else if (time >= 60 * 60 * 24) {
					time =
						parseInt(time / 3600.0 / 24) +
						"天" +
						parseInt(
							(parseFloat(time / 3600.0 / 24) - parseInt(time / 3600.0 / 24)) *
							24
						) +
						"小时" +
						parseInt(
							(parseFloat(time / 3600.0) - parseInt(time / 3600.0)) * 60
						) +
						"分钟" +
						parseInt(
							(parseFloat(
									(parseFloat(time / 3600.0) - parseInt(time / 3600.0)) * 60
								) -
								parseInt(
									(parseFloat(time / 3600.0) - parseInt(time / 3600.0)) * 60
								)) *
							60
						) +
						"秒";
				} else {
					time = parseInt(time) + "秒";
				}
			}
			return time;
		}
	},
	async onLoad(options) {
		this.id = options.id;
		if (options.userid) {
			this.userid = options.userid;
		}
	},
	async onShow(options) {
		// 渲染数据
		this.init();
		let table = uni.getStorageSync("nowTable");
		// 获取用户信息
		let res = await this.$api.session(table);
		this.user = res.data;
		this.btnColor = this.btnColor.sort(() => {
			return (0.5 - Math.random());
		});
	},
	destroyed: function() {
		//window.clearInterval(this.inter);
	},
	methods: {
		// 支付
		onPayTap() {
			uni.setStorageSync('paytable', 'baomingxinxi');
			uni.setStorageSync('payObject', this.detail);
			this.$utils.jump('../pay-confirm/pay-confirm?type=1')
		},
		// 收藏
		// 跨表
		onAcrossTap(tableName, crossOptAudit, statusColumnName, tips, statusColumnValue) {
			if (crossOptAudit == '是' && this.detail.sfsh != '是') {
				this.$utils.msg("请审核通过后再操作");
				return
			}
			if (tableName == 'renwukaishi' || tableName == 'renwujieshu') {
				this.$refs.pop.open()
				this.tableName = tableName
			}
			uni.setStorageSync('crossTable', 'baomingxinxi');
			uni.setStorageSync(`crossObj`, this.detail);
			uni.setStorageSync(`statusColumnName`, statusColumnName);
			uni.setStorageSync(`statusColumnValue`, statusColumnValue);
			uni.setStorageSync(`tips`, tips);
			if (statusColumnName != '' && !statusColumnName.startsWith("[")) {
				var obj = uni.getStorageSync('crossObj');
				for (var o in obj) {
					if (o == statusColumnName && obj[o] == statusColumnValue) {
						this.$utils.msg(tips);
						return
					}
				}
			}
			// this.$utils.jump(`../${tableName}/add-or-update?cross=true`);
		},
		async onStart(e){
			await this.$api.add(`${e}`, this.detail)
			this.$utils.msgBack('提交成功');
		},
		// 获取详情
		async init() {
			let res = await this.$api.info('baomingxinxi', this.id);
			this.detail = res.data;
			// 轮播图片
			this.swiperList = this.detail.tupian ? this.detail.tupian.split(",") : [];
		},

		// mescroll组件初始化的回调,可获取到mescroll对象
		mescrollInit(mescroll) {
			this.mescroll = mescroll;
		},

		/*下拉刷新的回调 */
		downCallback(mescroll) {
			this.hasNext = true
			mescroll.resetUpScroll()
		},

		/*上拉加载的回调: mescroll携带page的参数, 其中num:当前页 从1开始, size:每页数据条数,默认10 */
		async upCallback(mescroll) {
			mescroll.endSuccess(mescroll.size, this.hasNext);

		},

		onChatTap() {
			this.$utils.jump('../chat/chat')
		},
		// 下载
		download(url) {
			let _this = this;
			url = _this.$base.url + url;
			uni.downloadFile({
				url: url,
				success: (res) => {
					if (res.statusCode === 200) {
						_this.$utils.msg('下载成功');
						window.open(url);
					}
				}
			});
		},
		//
		onCartTabTap() {
			this.$utils.tab('../shop-cart/shop-cart')
		},
		// 添加评论
		async onCommentTap() {
			this.$utils.jump(`../discussbaomingxinxi/add-or-update?refid=${this.id}`)
		},
		onSHTap() {
			this.$refs.popup.open()
		},
		// 完成审核
		async onFinishTap() {
			if (!this.detail.sfsh) {
				this.$utils.msg('请选择审核状态');
				return
			}
			if (!this.detail.shhf) {
				this.$utils.msg('请填写审核回复');
				return
			}
			if (this.detail.sfsh == "通过") {
				this.detail.sfsh = '是'
			}
			if (this.detail.sfsh == "不通过") {
				this.detail.sfsh = '否'
			}
			await this.$api.update('baomingxinxi', this.detail);
			this.$utils.msg('审核成功');
			this.$refs.popup.close();
		},
		// 关闭窗口
		onCloseWinTap() {
			this.$refs.popup.close()
		},
		sfshChange(e) {
			console.log(this.detail)
			this.sfshIndex = e.target.value
			this.detail.sfsh = this.sfshOptions[this.sfshIndex]
		}
	}
}
